import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { PurchaseComponent } from './purchase/purchase.component';
import { PurchaseInvoiceComponent } from '../Invoice/purchase-invoice/purchase-invoice.component';
import { PurchaseInvoiceListComponent } from '../Invoice/purchase-invoice-list/purchase-invoice-list.component';
import { PuchaseReturnComponent } from '../Invoice/puchase-return/puchase-return.component';
import { PurchaseReturnListComponent } from '../Invoice/purchase-return-list/purchase-return-list.component';
import { SundryCreditorComponent } from '../Invoice/sundry-creditor/sundry-creditor.component';
import { PurchaseOrderComponent } from '../purchase-order/purchase-order.component';
import { PurchaseOrderListComponent } from '../purchase-order-list/purchase-order-list.component';
import { PaymentOutComponent } from '../payment-out/payment-out.component';
import { PaymentOutListComponent } from '../payment-out-list/payment-out-list.component';
import { RequestOrderComponent } from './request-order/request-order.component';


const routes: Routes = [
  { 
    path: '',
    component : PurchaseComponent,
    children : [
      { 
        path: '', 
        redirectTo: 'purchase-invoice-list',
        pathMatch: 'full'
      },
      {
        path: "purchase-invoice",
        component: PurchaseInvoiceComponent
      },
      {
        path: "purchase-invoice-list",
        component: PurchaseInvoiceListComponent,
      },
      {
        path: "sundry-creditor-list",
        component: SundryCreditorComponent
      },
      {
        path: "purchase-out",
        component: PaymentOutComponent
      },
      {
        path: "purchase-out-list",
        component: PaymentOutListComponent
      },
      {
        path: "purchase-order",
        component: PurchaseOrderComponent
      },
      {
        path: "purchase-order-list",
        component: PurchaseOrderListComponent
      },
      {
        path: "purchase-return",
        component: PuchaseReturnComponent,
      },
      {
        path: "purchase-return-list",
        component: PurchaseReturnListComponent,
      },
      {
        path: "request-order-list",
        component: RequestOrderComponent
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
]

@NgModule({
  declarations: [],
  imports: [RouterModule.forChild(routes)],

})
export class PurchsaeRoutingModule { }
